import React from 'react';
import "./Card.css";
import PropTypes from "prop-types";

const Card = ( {url, lang, fcolor, scolor} ) => {

    return (
        <div>
            <div className="card" >
                <img src={ url } alt="logo.png" />
                <h3>{ lang }</h3>
            </div>
        </div>
    )
}
//valida si la informacion es el tipo de dato que
// se requiere
Card.propTypes={ 
    lang: PropTypes.string,
    url: PropTypes.string
}

export default Card;
